import React from 'react'
import './index.less'
import NoneData from 'components/NoneData/all-none'
import TitleBar from 'components/topbar'
import connect from 'connect'
import { SearchBar, Button, WhiteSpace, WingBlank, Checkbox, List, Grid, Card, Flex, Modal } from 'antd-mobile';
const alert = Modal.alert;
@connect
class Home extends React.Component {
    constructor(props){
        super(props);
        this.state = {
          navTop: false,
          swiper: null,
          value: '美食',
          data: ['1', '2', '3'],
          imgHeight: 176,
           disabled: false,
        }
    }
    
    componentDidMount () {
       
    }

    del() {
        alert('温馨提示', '您确定要删除此收货地址吗？', [
          { text: '取消', onPress: () => {console.log('cancel')} },
          { text: '确定', onPress: () => {console.log('ok')} },
        ])

    }

    edit() {
      this.props.history.slideStatus = 'left'
      this.props.history.push(`/editaddress`)
    }

    render () {
        const Item = List.Item;
        const Brief = Item.Brief;
        const CheckboxItem = Checkbox.CheckboxItem;
        const data = [
          {
            img: 'https://zos.alipayobjects.com/rmsportal/dKbkpPXKfvZzWCM.png',
            title: '王凯',
            des: '河南省郑州市金水区北三环经三路经三名筑88号楼808室',
            value: '18212348561',
          },
          {
            img: 'https://zos.alipayobjects.com/rmsportal/dKbkpPXKfvZzWCM.png',
            title: '王凯',
            des: '河南省郑州市金水区北三环经三路经三名筑88号楼808室',
            value: '18212348561',
          },
        ];
        return (
            <div className="address-container">
            <TitleBar title="地址管理"/>
               {data.map((obj, index) => (
                <div  key={'myordercard' + index} >
                 <Card full={true}>
                       <Card.Header
                         extra={
                          <div style={{ }}>
                            <div><span className="jiatext">{obj.title}</span><span className="numtextdes">{obj.value}</span></div>
                            <div className="mid"></div>
                            <div className="destext">{obj.des}</div>
                          </div>
                         }
                       />
                       <Card.Body>
                        <CheckboxItem key="all" data-seed="logId" className="allcell" >
                            <div style={{float:'right'}}>
                            <Flex>
                                  <Flex.Item onClick={() => this.edit()}> <img style={{ height: '0.16rem', width: '0.15rem'}} src={require('../../images/dz_bj.png')} alt="" />编辑</Flex.Item>
                                  <Flex.Item onClick={() => this.del()}> <img style={{ height: '0.16rem', width: '0.15rem' }} src={require('../../images/dz_sc.png')} alt="" />删除</Flex.Item>
                            </Flex>
                          </div>
                        </CheckboxItem>
                       </Card.Body>
                       
                 </Card>
              </div>
               ))}
             
            <div className="cardfooter">
                <Button onClick={() => this.edit()}>新增地址</Button>
            </div>
     </div>
        )
    }
}
export default Home


